
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Toast from 'vux/src/components/toast/index.vue'


import serviceList from '../../components/ServiceList';
import moduleTitle from '../../components/ModuleTitle';
export default {
name:'chronicDiseases',
components:{Flexbox, FlexboxItem,serviceList,moduleTitle,Toast},
data(){
return {
showToast:false,
toastMsg:'',
tempService:[]
}
},
created(){
var vm = this;
this.$http.post('/service/findServiceByKeywords',{currentPage:1,showCount:10000,keywordsId:'2',type:1}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
vm.tempService = data.items;
}else{
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
console.log(data);
}
},(error) => {
console.log(error);
});
this.toastMsg = '';
}
}



